import React from "react";
import StatsCards from "./_components/stats-cards";
import { getAssessments } from "@/actions/interview";
import PerformanceChart from "./_components/performance-chart";

const InterviewPage = async() => {
  const assessments = await getAssessments()
  return (
    <div>
      <div>
        <h1 className="text-5xl md:text-6xl lg:text-7xl font-extrabold tracking-tight text-transparent bg-clip-text bg-gradient-to-r from-blue-600 via-purple-500 to-pink-500 animate-gradient-shift mb-5">
          Interview Preparation
        </h1>
        <div>
          <StatsCards assessments={assessments}/>
          <PerformanceChart assessments={assessments}/>
          <Quiz assessments={assessments}/>
        </div>
      </div>
    </div>
  );
};

export default InterviewPage;
